import React from 'react';
import s from './Tag.module.scss';
import clsx from "clsx";

export const Tag = ({text, onClick, isActive}) => {
    return (
        <button type="button"
                onClick={onClick}
                className={clsx(
                    s.item,
                    isActive && s.active
                )}>
            {text}
        </button>
    );
};