import React from 'react';

import './components/ui-components/TextButton/TextButton';
import './styles/main.scss';

import {MainButton} from "./components/ui-components/MainButton/MainButton";
import {Wrapper} from "./components/layout/Wrapper/Wrapper";
import {ModalCalc} from "./components/modals/ModalCalc/ModalCalc";
import {ModalWrapper} from "./components/modals/ModalWrapper/ModalWrapper";

function App() {
    const [showModal, setShowModal] = React.useState(false);
    const handleModalClose = () => setShowModal(false);
    return (
        <div className="App">
            <Wrapper>
                <MainButton
                    onClick={() => setShowModal(true)}
                    text="Налоговый вычет"
                    className="promo"/>
            </Wrapper>
            <ModalWrapper
                show={showModal}
                handleClose={handleModalClose}
            >
                <ModalCalc/>
            </ModalWrapper>
        </div>
    );
}

export default App;


