import React from 'react';
import clsx from 'clsx';
import s from './MainButton.module.scss';

export const MainButton = ({text, fill, className, onClick}) => {
    return (
        <button type="button"
                onClick={onClick}
                className={
                    clsx(
                        s.item, fill ? s.fill : s.empty,
                        s[className]
                    )}
        >
            {text}
        </button>
    );
};