import React from 'react';
import ReactDOM from 'react-dom';
import s from './ModalWrapper.module.scss';

export const ModalWrapper = ({show, handleClose, children}) => {
    const [shouldRender, setRender] = React.useState(show);

    const onAnimationEnd = () => {
        if (!show) setRender(false);
    }

    const onKeyCloseHandler = React.useCallback((e) => {
        if (e.keyCode === 27) {
            handleClose();
        }
    }, [handleClose]);

    React.useEffect(() => {
        if (show) {
            setRender(true);
            document.addEventListener('keydown', onKeyCloseHandler);
        }
        return () => document.removeEventListener('keydown', onKeyCloseHandler);
    }, [show, onKeyCloseHandler]);

    return ReactDOM.createPortal(
        <>
            {
                shouldRender &&
                <div style={{animation: `${show ? 'fadeIn' : 'fadeOut'} .3s forwards`}}
                     onAnimationEnd={onAnimationEnd}
                     className={s.body}
                     onClick={handleClose}
                >
                    <div onClick={e => e.stopPropagation()}>
                        {React.cloneElement(children, {handleClose})}
                    </div>
                </div>
            }
        </>,
        document.getElementById('modal')
    )
};