import React from 'react';
import clsx from 'clsx';
import s from './Input.module.scss';

export const Input = ({id, label, placeholder, isError, value, setValue}) => {
    return (
        <div className={clsx(
            s.field,
            isError && s.fieldError
        )}>
            <label htmlFor={id} className={s.label}>{label}</label>
            <input type="text"
                   className={s.item}
                   placeholder={placeholder}
                   value={value}
                   onChange={e => setValue(e.target.value)}
            />
            <span className={s.error}>Поле обязательно для заполнения</span>
        </div>
    );
};